import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { FormDirectiveComponent } from './template/form-directive.component';

const formRoutes: Routes = [
  { path: 'directive-form', component: FormDirectiveComponent }
];

@NgModule ({
  imports: [
    RouterModule.forChild(formRoutes)
  ],
  exports: [
    RouterModule
  ]
})

export class FormDirectiveRouting { }


