import { Directive, HostListener, forwardRef, Renderer, Attribute } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl } from '@angular/forms';

@Directive( {
    selector: '[myUnderline]',
    providers: [
        {provide: NG_VALIDATORS, useExisting: UnderlineDirective, multi: true}
    ]
})

export class UnderlineDirective implements Validator{
    constructor() {}
  
    validate(control: AbstractControl): {[key: string]: any} {
        if(control.value && control.value.length < 4){
            return {
                'minLengthName': {value: control.value}
            };
        } else if(control.value && control.value.length > 20) {
            return {
                'maxLengthName': {value: control.value}
            };
        }
    }
}



