import { Directive, HostListener, forwardRef, Renderer, Attribute } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl } from '@angular/forms';

@Directive( {
    selector: '[validateEqual]',
    providers: [
        {provide: NG_VALIDATORS, useExisting: ValidateDirective, multi: true}
    ]
})

export class ValidateDirective implements Validator{
    constructor(
        @Attribute('validateEqual') public validateEqual: string,
        @Attribute("reverse") public reverse: string) {}

    private get isReverse() {
        if (!this.reverse) return false;
        return this.reverse === "true" ? true: false;
    }
       
    validate(control: AbstractControl): {[key: string]: any} {
        let v = control.value;
        let e = control.root.get(this.validateEqual);
        
        // value not equal
        if (e && v !== e.value && !this.isReverse) return {
            validateEqual: false
        }
        
        //value equal and reverse
        if (e && v === e.value && this.isReverse) {
            delete e.errors["validateEqual"];
            if (!Object.keys(e.errors).length) 
            {
                e.setErrors(null);
            }
        }
        
        // value not equal and reverse
        if (e && v !== e.value && this.isReverse) {
            e.setErrors({ validateEqual: false })
        }
        return null;
    }
}



