import 'rxjs/add/operator/switchMap';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Todo, TodoService } from './todo-list.service';

import { Observable } from 'rxjs/Observable';

@Component({
  //selector: 'app-todo-list',
  templateUrl: './todo-list.component.html',
  styleUrls: ['./todo-list.component.css']
})
export class TodoListDetailComponent implements OnInit {
  task$: Observable<Todo>;
  
  constructor(
  	private route: ActivatedRoute,
    private router: Router,
    private service: TodoService) { }

  ngOnInit() {
  	this.task$ = this.route.paramMap.switchMap((params: ParamMap) => this.service.getTask(params.get('id')));
  }
  gotoTodoList(task: Todo) {
  	let taskId = task ? task.id: null;
  	this.router.navigate(['/todoList', {id: taskId}]);
  }
}



