import { Component, OnInit } from '@angular/core';
import { Hero } from './hero';
import { FormBuilder, FormControl, FormGroup, Validators, FormArray, AbstractControl } from '@angular/forms';

function validateName(c: FormControl) {
  let name_regexp = /^[a-zA-Z0-9]*$/;

  return name_regexp.test(c.value) ? null : {
    validateName: {
      valid: false
    }
  };
}

@Component({
  //selector: 'app-demo-form',
  templateUrl: './demo-form.component.html',
  styleUrls: ['./demo-form.component.css']
})

export class DemoFormComponent implements OnInit {
  submitted = false;
  powers = ['Smart', 'Speed', 'Weater Changer', 'Super Hot'];

  listPowers = [
    { id:  1, content: 'queen'},
    { id:  2, content: 'king'}
  ];
  
  powersType = [
          {Type: 'smart'},
          {Type: 'Nature'}
        ];

  //demo list checkbox show from clicked select box      
  listPowerType = [
    { Type: 'smart',id: 1, content: 'sport' }, 
    { Type: 'smart', id: 2, content: 'tech'},
    { Type: 'Nature', id: 1, content: 'fire' },
    { Type: 'Nature', id: 2, content: 'wind'}
  ];
  listPoTypeTemp = [];
  group2 = [];

  model = new Hero(18, 'Mark Zed', this.powers[1], 20, 'Chuck Sting');


  onSubmit() { this.submitted = true; }
  
  newHero() {
  	this.model = new Hero(21, '', '');
  }
  //TODO: Remove this when we are done
  get diagnostic() { return JSON.stringify( this.model ); }

  //ngOnInit () {}
  heroForm: FormGroup;
  c: FormGroup;

  ngOnInit() {
    let group = [];
    
    this.listPowers.forEach((l) => {
      group.push(new FormGroup({
        key: new FormControl(l.id),
        value: new FormControl(l.content),
        checked: new FormControl(false)
      }));
    });
    
    //var control = this.heroForm.controls[''] as FormGroup;
    let formControlArray = new FormArray(group);

    let formControlArrayDemo = new FormArray(this.group2);
  
    formControlArray.valueChanges.subscribe((v) => {
      this.heroForm.controls.selectedItems.setValue(this.mapItems(v));
    });

    formControlArrayDemo.valueChanges.subscribe((v) => {
      this.heroForm.controls.selectedItemsDemo.setValue(this.mapItemsDemo(v));
    });

  	this.heroForm = new FormGroup({
      name: new FormControl(this.model.name, [
        Validators.required, 
        Validators.minLength(5),
        //validateName
      ]),
      alterEgo: new FormControl(this.model.alterEgo),
      age: new FormControl(this.model.age),
      power: new FormControl(this.model.power),
      powersType: new FormControl(this.powersType),
      //listPows: new FormControl('', Validators.required),
      items: formControlArray,
      typeItems: formControlArrayDemo,
      selectedItems: new FormControl(this.mapItems(formControlArray.value), Validators.required),
      selectedItemsDemo: new FormControl(this.mapItemsDemo(formControlArrayDemo.value), Validators.required)
    });
    
    this.c = new FormGroup({
      l: new FormControl('', Validators.required)
    });
    
  }

  get name() { return this.heroForm.get('name'); }

  get power() { return this.heroForm.get('power'); }

  mapItems(items) {
    let selectedItems = items.filter((l) => l.checked).map((l) => l.key);
    console.log(items.filter((l) => l.checked));
    return selectedItems.length ? selectedItems : null;
  }
  
  mapItemsDemo(items) {
    let selectedItemsDemo = items.filter((l) => l.checkedDemo).map((l) => l.key);
    console.log(items.filter((l) => l.checkedDemo));
    return selectedItemsDemo.length ? selectedItemsDemo : null;
  }

  changeList(selectBox : string) {
    console.log(this.listPowerType);
    this.listPoTypeTemp = [];
    this.group2 = [];
    if (this.listPoTypeTemp.length <= 0) {
      for(var i = 0; i < this.listPowerType.length; i++) {
        if (selectBox === this.listPowerType[i].Type) {
          this.listPoTypeTemp.push(this.listPowerType[i]);
        }
      };
    }

    this.listPoTypeTemp.forEach((l) => {
      this.group2.push(new FormGroup({
        key: new FormControl(l.id),
        value: new FormControl(l.content),
        checkedDemo: new FormControl(false)
      }));
    });
    
    let formControlArrayDemo = new FormArray(this.group2);    
    formControlArrayDemo.valueChanges.subscribe((v) => {
      this.heroForm.controls.selectedItemsDemo.setValue(this.mapItemsDemo(v));
    });
    return this.heroForm.setControl('typeItems', formControlArrayDemo);
    // return this.listPoTypeTemp;
  }
}



