import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';
import { Observable } from 'rxjs/Observable';
import { Todo, TodoService } from './todo-list.service';

@Component({
  //selector: 'app-todo-list',
  template: `
    <h2>To do List</h2>
    <ul class="items todo">
      <li *ngFor="let task of tasks$ | async"
        [class.selected]="task.id === selectedId">
        <label class="contain">
          <input type="checkbox" id="#{{ task.id }}" />
          <span class="checkmark"></span>
        </label>
        <a [routerLink]="['/task', task.id]">
          <!--<span class="badge">{{ task.id }}</span>-->
          {{ task.content }}
        </a>
      </li>
    </ul>
  `,
  styleUrls: ['../heroes/heroes.component.css', './todo-list.component.css']
})
export class TodoListComponent implements OnInit {

  tasks$: Observable<Todo[]>;

  private selectedId: number;

  constructor(
  	private service: TodoService,
  	private route: ActivatedRoute) { }

  ngOnInit() {
  	this.tasks$ = this.route.paramMap.switchMap((params: ParamMap) => {
      this.selectedId = +params.get('id');
      return this.service.getTasks();
  	});
  }
}



