import 'rxjs/add/observable/of';
import 'rxjs/add/operator/map';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

export class Todo {
	constructor(public id: number, public content: string) { }
}

const TODOLIST = [
  new Todo(1, 'work1'),
  new Todo(2, 'work2'),
  new Todo(3, 'work3'),
  new Todo(4, 'work4'),
  new Todo(5, 'work5'),
  new Todo(6, 'work6'),
  new Todo(7, 'work7'),
  new Todo(8, 'work8'),
  new Todo(9, 'work9'),
  new Todo(10, 'work10')
];

@Injectable()
export class TodoService {
	getTasks() { return Observable.of(TODOLIST); }

	getTask(id: number | string) {
      return this.getTasks().map(tasks => tasks.find(task => task.id === +id));
	}
}



