import { Component, OnInit, Input } from '@angular/core';
import { ValidateDirective } from '../shared/form-directive.directive';

@Component({
  selector: 'app-form-directive',
  templateUrl: './form-directive.component.html',
  styleUrls: ['./form-directive.component.css']
})
export class FormDirectiveComponent implements OnInit {

  powers = ['Smart', 'fast', 'fire'];
  hero = {name: 'D. Saw',password: '', confirmPassword: '' , alterEgo: 'Dr. Why', power: this.powers[0]};
  url = '';
  powersType = [
    {Type: 'smart'},
    {Type: 'Nature'}
  ];

  //demo list checkbox show from clicked select box      
  listPowerType = [
  { Type: 'smart',id: 1, content: 'sport' }, 
  { Type: 'smart', id: 2, content: 'tech'},
  { Type: 'Nature', id: 1, content: 'fire' },
  { Type: 'Nature', id: 2, content: 'wind'}
  ];
  listPoTypeTemp = [];
  group2 = [];
  @Input() public selectBox: string;
   
  constructor() { }

  ngOnInit() {
    this.selectedOptions();    
  }
  
  selectedOptions() { // right now: ['1','3']
    this.listPoTypeTemp = [];
    this.group2 = [];
    if (this.listPoTypeTemp.length <= 0) {
      for(var i = 0; i < this.listPowerType.length; i++) {
        if (this.selectBox === this.listPowerType[i].Type) {
          this.listPoTypeTemp.push(this.listPowerType[i]);
        }
      };
    }
  }
  onChangeFile(event: any) {
    //console.log(event);
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
  
      reader.onload = (event:any) => {
        this.url = event.target.result;
      }
  
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  changeList(selectBox : string) {
    console.log(this.listPowerType);
    this.listPoTypeTemp = [];
    this.group2 = [];
    if (this.listPoTypeTemp.length <= 0) {
      for(var i = 0; i < this.listPowerType.length; i++) {
        if (selectBox === this.listPowerType[i].Type) {
          this.listPoTypeTemp.push(this.listPowerType[i]);
        }
      };
    }
    return this.listPoTypeTemp;
  }
  // val = false;
  // name="";
}



