//import { Directive, Input, TemplateRef, ViewContainerRef } from '@angular/core';
import { Directive, HostListener, forwardRef, Renderer, Attribute, Input } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl } from '@angular/forms';

@Directive({ 
    selector: '[changeListBox]',
    providers: [
        {provide: NG_VALIDATORS, useExisting: IfDirective, multi: true}
    ]
})

export class IfDirective implements Validator{
    constructor() {}
    @Input('changeListBox') listCheckbox: any;
    
    validate(control: AbstractControl): {[key: string]: any} {
        let listBox = control.value;
        
        //let selected: any = control.filter((item) => item.checked === true);
        if(listBox == false) {
          return control;
        } else {
          console.log(listBox);
          return null;
        }
    }
}



