import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule }    from '@angular/forms';

import { FormDirectiveComponent } from './template/form-directive.component';
import { FormDirectiveRouting } from './form-directive-routing.module';

import { UnderlineDirective } from './shared/form-underline.directive';
import { ValidateDirective } from './shared/form-directive.directive';
import { IfDirective } from './shared/form-structure.directive';

@NgModule ({
  imports: [
    CommonModule,
    FormsModule,
    FormDirectiveRouting
  ],
  declarations: [
    ValidateDirective,
    UnderlineDirective,
    IfDirective,
    FormDirectiveComponent
  ],
  exports: [
      ValidateDirective,
      IfDirective,
      // Export new directive
      UnderlineDirective
  ],
  providers: []
})

export class FormDirectiveModule { }


