import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { ReactiveFormsModule } from '@angular/forms';
import { FormsModule } from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';

import { HeroesModule } from './heroes/heroes.module';
//import { HeroListComponent } from './heroes/hero-list.component'; delete because it is in heroes.module
//import { HeroDetailComponent } from './heroes/hero-detail.component';

import { CrisisListComponent } from './crisis-center/crisis-center.component';
import { PageNotFoundComponent } from './not-found.component';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// import { TodoListComponent } from './todo-list/todo-list.component';

import { TodoListModule } from './todo-list/todo-list.module';
import { DemoFormModule } from './demo-form/demo-form.module';
import { FormDirectiveModule } from './form-directive/form-directive.module';

@NgModule({
  declarations: [
    CrisisListComponent, 
    AppComponent,
    PageNotFoundComponent
  ],
  imports: [
    BrowserAnimationsModule,
    ReactiveFormsModule,
    FormsModule,
    BrowserModule,
    FormDirectiveModule,
    DemoFormModule,
    TodoListModule,
    HeroesModule,
    AppRoutingModule
    // other imports here
  ],
  providers: [],
  bootstrap: [ AppComponent ]
})

export class AppModule { }



